;AADhC;IAII,+BAA+B;IAC/B,mBAAmB,EAAA;AALvB;MAQM,YAAY,EAAA;AARlB;IAcM,eAAe;IACf,cAAc,EAAA;AAMpB;EACE,aAAa,EAAA;AAIf;EAEI,4BAA4B;EAC5B,gBAAgB;EAChB,kBAAkB,EAAA;AAJtB;IAOM,mBAAmB,EAAA;AAPzB;EAYI,aAAa,EAAA;AAZjB;EAeI,qBAAqB;EACrB,UAAU;EACV,SAAS,EAAA;AAjBb;IAoBM,4BAA4B;IAC5B,SAAS;IACT,cAAc;IACd,gBAAgB,EAAA;AAvBtB;MA0BQ,aAAa,EAAA;AA1BrB;MA8BQ,iBAAiB;MACjB,WAAW;MACX,UAAU,EAAA;AAhClB;MAoCQ,gBAAgB;MAChB,SAAS;MACT,kCAAkC,EAAA;AAtC1C;MA0CQ,aAAa,EAAA;AAOrB;EACE,aAAa,EAAA","file":"print.scss","sourcesContent":["/* Active Admin Print Stylesheet */\n@import \"~@activeadmin/activeadmin/src/scss/print\";\n","/* Active Admin Print Stylesheet */\n\n// Set colors used elsewhere\n$primary-color: black;\n$text-color: black;\n\n// Normalize\n@import \"./normalize\";\n\n// Partials\n@import \"./typography\";\n\nbody {\n  font-family: Helvetica, Arial, sans-serif;\n  line-height: 150%;\n  font-size: 72%;\n  background: #fff;\n  width: 99%;\n  margin: 0;\n  padding: .5%;\n  color: $text-color;\n}\n\na {\n  color: $text-color;\n  text-decoration: none;\n}\n\nh3 {\n  font-weight: bold;\n  margin-bottom: .5em;\n}\n\n// Header\n#header {\n  float: left;\n\n  #tabs, .tabs, #utility_nav {\n    display: none;\n  }\n\n  h1{\n    font-weight: bold;\n  }\n}\n\n.flashes {\n  display: none;\n}\n\n#title_bar {\n  float: right;\n\n  h2 {\n    line-height: 2em;\n    margin: 0;\n  }\n\n  .breadcrumb, #titlebar_right {\n    display: none;\n  }\n}\n\n// Content\n#active_admin_content {\n  border-top: thick solid black;\n  clear: both;\n  margin-top: 2em;\n  padding-top: 3em;\n}\n\n// Footer\n#footer {\n  display: none;\n}\n\n// Tables\n.table_tools {\n  ul {\n    padding: 0;\n    margin: 0;\n    list-style-type: none;\n\n    li {\n      display: none;\n      padding: 0;\n      margin-bottom: 1em;\n\n      &.scope.selected, &.index.selected {\n        display: block;\n\n        &:before {\n          content: \"Showing \";\n        }\n\n        a {\n          font-weight: bold;\n        }\n\n        span {\n          display: inline-block;\n          font-weight: normal;\n          font-size: .9em;\n        }\n      }\n    }\n\n  }\n}\n\ntable {\n  margin-bottom: 1.5em;\n  text-align: left;\n  width: 100%;\n\n  thead {\n    display: table-header-group;\n\n    th {\n      background: none;\n      border-bottom: medium solid black;\n      font-weight: bold;\n\n      a{\n        text-decoration: none;\n      }\n    }\n  }\n\n  th, td {\n    padding: .5em 1em;\n\n    .member_link {\n      display: none;\n    }\n  }\n\n  td {\n    border-bottom: thin solid black;\n  }\n\n  tr{\n    page-break-inside: avoid;\n  }\n}\n\n// Index\n#index_footer, .pagination_information {\n  display: none;\n}\n\n.index_grid {\n  td {\n    border: none;\n    text-align: center;\n    vertical-align: middle;\n\n    img {\n      max-width: 1in;\n    }\n  }\n}\n\n// Show\n.panel {\n  border-bottom: thick solid #ccc;\n  margin-bottom: 3em;\n  padding-bottom: 2em;\n  page-break-inside: avoid;\n\n  &:last-child {\n    border-bottom: none;\n  }\n}\n\n.comments {\n  form {\n    display: none;\n  }\n\n  .active_admin_comment {\n    border-top: thin solid black;\n    padding-top: 1em;\n\n    .active_admin_comment_meta {\n      h4 {\n        font-size: 1em;\n        font-weight: bold;\n        float: left;\n        margin-right: .5em;\n        margin-bottom: 0;\n      }\n\n      span {\n        font-size: .9em;\n        font-style: italic;\n        vertical-align: top;\n      }\n    }\n\n    .active_admin_comment_body {\n      clear: both;\n      margin-bottom: 1em;\n    }\n  }\n}\n\n\n// Attribute Tables\n.attributes_table {\n  border-top: medium solid black;\n\n  th {\n    border-bottom: thin solid black;\n    vertical-align: top;\n\n    &:after {\n      content: ':';\n    }\n  }\n\n  td {\n    img {\n      max-height: 4in;\n      max-width: 6in;\n    }\n  }\n}\n\n// Sidebars\n#filters_sidebar_section {\n  display: none;\n}\n\n// Forms\nform {\n  fieldset {\n    border-top: thick solid #ccc;\n    padding-top: 2em;\n    margin-bottom: 2em;\n\n    &:last-child {\n      border-bottom: none;\n    }\n  }\n\n  .buttons, abbr {\n    display: none;\n  }\n  ol {\n    list-style-type: none;\n    padding: 0;\n    margin: 0;\n\n    li{\n      border-top: thin solid black;\n      margin: 0;\n      padding: 1em 0;\n      overflow: hidden;\n\n      &.password, &.hidden {\n        display: none;\n      }\n\n      label {\n        font-weight: bold;\n        float: left;\n        width: 20%;\n      }\n\n      input, textarea, select {\n        background: none;\n        border: 0;\n        font: Arial, Helvetica, sans-serif;\n      }\n\n      input[type=file] {\n        display: none;\n      }\n\n    }\n  }\n}\n\n.unsupported_browser {\n  display: none;\n}\n","/*! normalize.css v7.0.0 | MIT License | github.com/necolas/normalize.css */\n\n/* Document\n   ========================================================================== */\n\n/**\n * 1. Correct the line height in all browsers.\n * 2. Prevent adjustments of font size after orientation changes in\n *    IE on Windows Phone and in iOS.\n */\n\nhtml {\n  line-height: 1.15; /* 1 */\n  -ms-text-size-adjust: 100%; /* 2 */\n  -webkit-text-size-adjust: 100%; /* 2 */\n}\n\n/* Sections\n   ========================================================================== */\n\n/**\n * Remove the margin in all browsers (opinionated).\n */\n\nbody {\n  margin: 0;\n}\n\n/**\n * Add the correct display in IE 9-.\n */\n\narticle,\naside,\nfooter,\nheader,\nnav,\nsection {\n  display: block;\n}\n\n/**\n * Correct the font size and margin on `h1` elements within `section` and\n * `article` contexts in Chrome, Firefox, and Safari.\n */\n\nh1 {\n  font-size: 2em;\n  margin: 0.67em 0;\n}\n\n/* Grouping content\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n * 1. Add the correct display in IE.\n */\n\nfigcaption,\nfigure,\nmain { /* 1 */\n  display: block;\n}\n\n/**\n * Add the